import React, { Component } from "react";
// import ReactTable from "react-table";
import axios from "axios";
import { Link } from "react-router-dom";
export default class ViewProducts extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [
        {
          name: "priducn11dddvffgdgdd",
          disocunt: "20",
          price: "1000",
        },
      ],

      columns: [
        {
          Name: "Name",
          accessor: "name",
        },
        {
          Price: "Price",
          accessor: "price",
        },
        {
          Discount: "Discount",
          accessor: "discount",
        },
      ],
      edit: false,
      cart: [],
    };
  }

  componentDidMount() {
    this.getProducts();
  }
  getProducts = () => {
    axios.get(`http://localhost:3000`).then((res) => {
      console.log("call api from frontend");
      console.log(res.data.payload);
      this.setState({
        data: res.data.payload,
      });
    });
  };
  sortData = (keyName, keyValue) => {
    axios
      .post(`http://localhost:3000/sort`, { keyName, keyValue })
      .then((res) => {
        console.log("call api from frontend");
        console.log(res.data.payload);
        this.setState({
          data: res.data.payload,
        });
      });
  };
  deleteProduct = (id) => {
    axios
      .delete(`http://localhost:3000/${id}`)
      .then((res) => {
        console.log("delete ", res);
        alert("Delete success");
        this.getProducts();
      })
      .catch((e) => {
        alert("Delete error");
      });
  };

  addToCart = (val) => {
    let { cart } = this.state;
    var newCart = cart;
    this.setState({
      cart: [...cart, { ...val, quantity: 1 }],
    });
    newCart.map((v) => {
      if (v._id === val._id) {
        v.quantity = v.quantity + 1;
      }
    });
    console.log("new cart -- > ", newCart);
    // this.setState({ cart: newCart });
  };
  render() {
    let { data, cart } = this.state;
    console.log("car t data values --> ", this.state);
    return (
      <div>
        <h1>View Product</h1>
        <Link to="/add">Add Product</Link> <br />
        <Link to="/cart">View Cart</Link>
        <table>
          <thead>
            <th>Product Name</th>
            <th>
              Price{" "}
              <button
                onClick={() => {
                  this.sortData("price", 1);
                }}
              >
                Sort asc
              </button>{" "}
              <button
                onClick={() => {
                  this.sortData("price", -1);
                }}
              >
                Sort asc
              </button>
            </th>
            <th>
              Discount
              <button
                onClick={() => {
                  this.sortData("discount", 1);
                }}
              >
                Sort asc
              </button>{" "}
              <button
                onClick={() => {
                  this.sortData("discount", -1);
                }}
              >
                Sort asc
              </button>
            </th>
          </thead>
          <tbody>
            {data?.length &&
              data.map((val, i) => (
                <tr key={val._id}>
                  <td>{val.name}</td>
                  <td>{val.price}</td>
                  <td>{val.discount}</td>
                  <td>
                    <button onClick={() => this.deleteProduct(val._id)}>
                      Delete
                    </button>
                  </td>
                  <td>
                    <Link to={`/edit/${val._id}`}>Update</Link>
                  </td>
                  <td>
                    <button onClick={() => this.addToCart(val)}>
                      Add to Cart
                    </button>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
        <h1>Your cart</h1>
        <table>
          <thead>
            <th>Name</th>
            <th>Price</th>
            <th>Discount</th>
            <th>Quantity</th>
          </thead>
          <tbody>
            {cart.map((val) => (
              <tr key={val._id}>
                <td>{val.name}</td>
                <td>{val.price}</td>
                <td>{val.discount}</td>
                <td>{val.quantity}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}
